<template>
	<div class="table-box">
		<el-table ref="multipleTable" :data="tableData3" border tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column label="车辆信息" width="330px">
				<template scope="scope">
					{{ scope.row.date }}{{ scope.row.cal}}{{ scope.row.length}}{{ scope.row.zhongliang}}{{ scope.row.rongji}}
				</template>
			</el-table-column>
			<el-table-column prop="name" label="司机信息" width="240px">
			</el-table-column>
			<el-table-column prop="address" label="定位信息" show-overflow-tooltip width="330px">
			</el-table-column>
			<el-table-column label="操作" width="340px">
				<template scope="scope">
					<el-button type="text" size="small">
						车辆定位
					</el-button>
					<el-button type="text" size="small">
						历史轨迹
					</el-button>
					<el-button type="text" size="small">
						申请修改
					</el-button>
					<el-button class='removess' @click="open2(scope.$index)" type="text" size="small">
						删除
					</el-button>
				</template>
			</el-table-column>
		</el-table>
	</div>

</template>

<script>
	export default {
		data() {
			return {
				tableData3: [{
					date: '泸DG5060',
					cal:'厢式车',
					length:'9.6米',
					zhongliang:'十吨  ',
					rongji:"30方",
					name: '王小虎   ',
					address: '山东省烟台市莱阳市G204(龙门西路)'
				}, {
					date: '泸DG5060',
					cal:'厢式车',
					length:'9.6米',
					zhongliang:'十吨  ',
					rongji:"30方",
					name: '王小虎   ',
					address: '山东省烟台市莱阳市G204(龙门西路)'
				}, {
					date: '泸DG5060',
					cal:'厢式车',
					length:'9.6米',
					zhongliang:'十吨  ',
					rongji:"30方",
					name: '王小虎   ',
					address: '山东省烟台市莱阳市G204(龙门西路)'
				}],
				multipleSelection: []
			}
		},

		methods: {
			toggleSelection(rows) {
				if(rows) {
					rows.forEach(row => {
						this.$refs.multipleTable.toggleRowSelection(row);
					});
				} else {
					this.$refs.multipleTable.clearSelection();
				}
			},
			handleSelectionChange(val) {
				this.multipleSelection = val;
			},
			open2(index) {
				let This = this;
				this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning',
					callback:function(type){
						if(type=="confirm"){
							This.tableData3.splice(index, 1);
						}
					}
				})
			}

		}
	}
</script>

<style lang='less'>
	.table-box {
		margin: 40px 30px 30px 30px;
		tbody {
			cursor: pointer;
			tr {
				height: 100px;
			}
		}
	}
</style>


